<template>
  <header>
    <h1 @click="toHello">{{title}}</h1>
  </header>
</template>

<script>
    export default {
        name: "Header",
        data() {
            return {
                title: 'Third Vue CLI Project By Shenke',
            }
        },
        props: {},
        components: {},
        methods: {
            toHello: function () {
                this.$router.push({
                    name: 'hello',
                    params: {
                        message: 'message from header'
                    }
                })
            }
        },
        computed: {},
        watch: {},
    }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  header {
    padding: 20px;
    text-align: center;
    background: royalblue;
  }

  header h1 {
    color: white;
  }


</style>



